#CreateRequestForm {
    .title {
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: #6D711E;
        margin-bottom: 1rem;
    }

    .sub-title {
        font-weight: 700;
        font-size: 15px;
        line-height: 18px;
        color: #5F5974;
        margin-bottom: 1rem;
    }

    .header-inpts {
        gap: 15px;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    }

    .inpts-pairs {
        column-gap: 15px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
}